import AirDatepicker from "air-datepicker";
import "air-datepicker/air-datepicker.css";
import HystModal from "./modules/hystmodal.js";

// BURGER MENU
const btnBurger = document.querySelector(".m-burger-menu");
const btnCloseBurger = document.querySelector(".m-close-btn");
const burgerContent = document.querySelector(".b-header-nav ");

btnBurger.addEventListener("click", openMobile);
btnCloseBurger.addEventListener("click", function () {
  burgerContent.classList.remove("active-menu");
  btnBurger.children[0].classList.remove("opened");
});
function openMobile() {
  burgerContent.classList.add("active-menu");
}

// FOUNDATION
var app = {};

$(document).ready(function () {
  $(document).foundation();

  // $(".js-select").select2({
  //   placeholder: 'ТЕСТ',
  // });

  $(function () {
    $(document).on("click", ".js-select__input", function () {
      $(this).parents(".js-select").toggleClass("active");
    });
    $(document).mouseup(function (e) {
      // событие клика по веб-документу
      var div = $(".js-select.active"); // тут указываем ID элемента
      if (
        !div.is(e.target) && // если клик был не по нашему блоку
        div.has(e.target).length === 0
      ) {
        // и не по его дочерним элементам
        div.removeClass("active"); // скрываем его
      }
    });
    $(document).on("click", ".active .js-select__list span", function () {
      var id = $(this).data("id");
      var parent = $(this).parents(".js-select");
      parent.find(".js-select__input").text($(this).text());
      parent.find("input").val(id).attr("value", id);
      parent.removeClass("active");
    });
  });
});

