// MAIN BANNER
const swiperMainBanner = new Swiper(".l-main-banner", {
  loop: true,
  pagination: {
    el: ".l-main-banner__pagination",
  },
  autoplay: {
    delay: 2000,
  },
});

const swiperNewsMain = new Swiper(".swiper-news", {
  sliderPerView: "auto",
  spaceBetween: 40,
  navigation: {
    nextEl: ".swiper-news__btn-next",
    prevEl: ".swiper-news__btn-prev",
  },
});

let dpMin, dpMax;

dpMin = new AirDatepicker("#datepick-1", {
  // isMobile: true,
  // autoClose: true,
  onSelect({ date }) {
    dpMax.update({
      minDate: date,
    });
  },
});

dpMax = new AirDatepicker("#datepick-2", {
  onSelect({ date }) {
    dpMin.update({
      maxDate: date,
    });
  },
});

// vacancies open detail

const vacanciesOpenDetailBtn = document.querySelectorAll(".l-detail-open");

vacanciesOpenDetailBtn.forEach((el) => {
  el.addEventListener("click", openFadeDetail);
});

function openFadeDetail(el) {
  let btnTarget = el.currentTarget;
  btnTarget.offsetParent.children[1].classList.toggle("active");
  btnTarget.nextSibling.classList.toggle("active");
}

const popupVacancies = new HystModal({
  linkAttributeName: "data-hystmodal",
});

$(".input-file input[type=file]").on("change", function () {
  let file = this.files[0];
  $(this).next().html(file.name);
});

